.footer {
  padding-top: 160px;
  padding-bottom: 160px;
  background-color: $secondary;
  &-block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    // grid-column-gap: 30px;
  }
  &-item.footer-item-link-mobile {
    display: none;
  }
  &-item + &-item {
    margin-left: 20px;
  }
  &-item {
    &-address + &-address {
      margin-top: 65px;
    }
    &-address {
      &-wrap {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
      }
      &__title {
        font-size: 22px;
        line-height: 28px;
        color: $white;
        margin-bottom: 28px;
      }
      address {
        p {
          margin-bottom: 16px;
        }
        p, a {
          font-size: 18px;
          line-height: 26px;
          text-decoration: none;
          color: rgba(255, 255, 255, .5);
          font-style: normal;
        }
      }
    }
    &-link {
      &-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      p {
        font-size: 18px;
        line-height: 22px;
        color: $white;
        margin-bottom: 35px;
      }
      a {
        text-decoration: none;
        font-size: 22px;
        line-height: 27px;
        color: $primary;
        padding-bottom: 10px;
        // border-bottom: 1px solid $primary;
      }
    }
    &-social {
      display: flex;
      align-items: center;
      a + a {
        margin-left: 13%;
      }
      a {
        text-decoration: none;
        font-size: 18px;
        line-height: 22px;
        color: $primary;
        padding-bottom: 6px;
        // border-bottom: 1px solid $primary;
      }
    }
    &-nav {
      width: 50%;
      &-wrap {
        display: flex;
      }
      ul {
        li + li {
          margin-top: 50px;
        }
        li {
          a {
            font-size: 18px;
            line-height: 22px;
            text-decoration: none;
            color: $white;
          }
        }
      }
    }
  }
}