@media (min-width: 1025px) {
  // .section-main-bottom:hover {
  //   box-shadow: -10px 0 30px rgba(30, 37, 43, .3);
  //   transition: $cubic;
  // }
  .link-line.type-1:hover {
    &::after {
      width: 100%;
    }
  }
  .link-line.type-2:hover {
    &::after {
      width: 0;
    }
  }
  // gumb 
  .header__gumb:hover {
    &::before {
      transform: translate(0, -12px);
    }
    &::after {
      transform: translate(0, 12px);
    }
  }
  .services-grid.grid.type-1 {
    .services-grid-item:not(:first-child):hover {
      border-color: $black
    }
  }
  .view-videos-grid-item:hover {
    .view-videos-grid-item-img::after {
      opacity: 1;
    }
    .play-video {
      right: 50%;
      bottom: 50%;
      transform: translate(50%, 50%);
      svg {
        width: 100px;
        height: 100px;
      }
    }
  }
  .cases-grid-item:hover {
    .cases-grid-item-img {
      &::after {
        opacity: 1;
      }
      img {
        transform: scale(1.2);
        transition: 9s cubic-bezier(.1,.2,.7,1);
      }
    }
  }
  .header-menu-nav li:hover {
    & > span:first-child, a {
      &::after {
        width: 100%;
      }
    }
  }
  .team-grid.grid.type-1 .team-grid-item.wrap-icon-hover:hover {
    .team-grid-item-content {
      opacity: 1;
    }
  }
  .read-more-list-item:hover {
    .read-more-list-item-btn {
      animation: btnAction .4s ease-in forwards;
    }
  }
}

