@media screen and (max-width: 1024px) {
  .grid.type-1 {
    grid-template-columns: repeat(2,1fr);
    grid-gap: 30px;
  }
  .header.sticky {
    height: 84px;
  }
  .header {
    &-nav {
      display: none;
    }
    &-main {
      justify-content: flex-end;
    }
    &__logo {
      max-width: 106px;
    }
    &-block {
      padding: 21px 0;
    }
    &-menu.active {
      .header-back-menu {
        opacity: 1;
        visibility: visible;
      }
    }
    &-menu {
      &-wrapper {
        padding-top: 32px;
      }
      &-content {
        &-item.hide {
          display: block;
          left: 100%;
        }
        &-item {
          position: fixed;
          width: 100%;
          height: calc(100% - 86px);
          top: 86px;
          left: 0;
          max-width: 100%;
          background-color: $white;
          transition: $cubic;
          padding-left: 30px;
          padding-right: 30px;
          padding-bottom: 40px;
          overflow: auto;
        }
      }
      &-nav {
        li.item-active {
          & > span:first-child, a {
            &::after {
              display: none;
            }
          }
        }
      }
      &-close {
        &-wrap {
          top: 32px;
        }
        svg {
          width: 26px;
          height: 26px;
        }
      }
      &-block {
        height: 100%;
      }
    }
    &-back {
      &-menu {
        display: block;
        width: fit-content;
        margin-bottom: 24px;
        opacity: 0;
        visibility: hidden;
      }
    }
  }
  .section-main {
    margin-top: -84px;
    padding-top: 84px;
    background-image: url('../img/section-main-bg-mobile.jpg');
    &-content {
      padding-top: 9%;
      width: 100%;
      h1 {
        font-size: 38px;
        line-height: 56px;
      }
    }
    &-bottom {
      width: 100%;
      max-width: 500px;
    }
    &-photo {
      display: none;
    }
  }
  .services {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .services-grid.grid.type-1 {
    .services-grid {      
      &-item:nth-child(even) {
        background-color: transparent;
      }
      &-item:nth-child(4n+3), &-item:nth-child(4n+2) {
        background-color: #f7f9fb;
      }
      &-item {
        &-top {
          &-icon {
            max-width: 80px;
            margin-bottom: 30px;
          }
        }
      }
    }
  }
  .about-me {
    padding-top: 40px;
    padding-bottom: 40px;
    &-block {
      flex-direction: column;
      .about-me-left, .about-me-right {
        width: 100%;
      }
      .about-me-left {
        padding-top: 0;
        margin-bottom: 20px;
        p {
          font-size: 26px;
        }
      }
      .about-me-right {
        p {
          font-size: 34px;
          line-height: 48px;
        }
      }
    }
  }
  .team {
    .team-grid.grid.type-1 {
      .team-grid-item.wrap-icon-hover.active {
        .team-grid-item-content {
          display: flex;
        }
      }
      .team-grid-item.wrap-icon-hover {
        .team-grid-item-content {
          display: none;
          animation: teamShowCard .3s ease-in-out forwards;
          &__name {
            font-size: 24px;
            line-height: 32px;
          }
          &__text {
            font-size: 16px;
            line-height: 28px;
          }
        }
      }
    }
  }
  .evidence {
    &-list {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 60px;  
    }
  }
  .buy {
    background-position: 50% center;
    &-item {
      width: 55%;
      &__text {
        span {
          display: block;
        }
      }
    }
  }
  .cases {
    padding-top: 100px;
    padding-bottom: 50px;
    &-grid {
      &__title {
        font-size: 24px;
        line-height: 28px;
      }
    }
  }
  .view-videos {
    .play-video {
      width: 100%;
      height: 100%;
      right: auto;
      bottom: auto;
      top: 0;
      left: 0;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      svg {
        margin-right: 30px;
        margin-bottom: 30px;
      }
    }
    &-grid {
      &__subtitle {
        font-size: 24px;
        line-height: 34px;
      }
      &-item {
        &-img {
          margin-bottom: 25px;
        }
      }
    }
  }
  .read-more {
    &-list {
      &-item {
        &__title {
          font-size: 28px;
        }
      }
    }
    &-top {
      margin-bottom: 65px;
    }
  }
  .subscription {
    background-position: calc(100% + 177px) center;
    &-block {
      flex-direction: column;
      align-items: flex-start;
    }
    &-item + &-item {
      margin-top: 40px;
    }
    &-item, &-item:last-child {
      width: 75%;
      padding-left: 0;
      justify-content: flex-start;
    }
  }
  .connection {
    padding-top: 80px;
    padding-bottom: 80px;
    h2 {
      margin-bottom: 44px;
    }
    &-block {
      flex-direction: column;
    }
    &-text, &-form {
      width: 100%;
    }
    &-text {
      margin-bottom: 40px;
    }
    &-form {
      form {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 100%;
        .input-wrap + .input-wrap {
          margin-top: 0;
        }
        .input-wrap {
          width: calc(50% - 20px);
          margin-bottom: 40px;
        }
      }
      &-check {
        margin-top: 10px;
        width: 100%;
      }
    }
  }
  .footer {
    padding-top: 80px;
    padding-bottom: 80px;
    &-block {
      grid-template-columns: 40% 1fr;
      grid-column-gap: 30px;
      grid-row-gap: 60px;
    }
    &-item {
      &-link {
        width: 40%;
        margin-right: 30px;
        a {
          display: block;
          width: fit-content;
          font-size: 16px;
          line-height: 18px;
        }
      }
    }
    &-item.footer-item-link-wrap {
      grid-row: 2/3;
      grid-column: 1/3;
      flex-direction: row;
      align-items: flex-end;
      .footer-item-social {
        flex-grow: 1;
      }
    }
    &-item.footer-item-nav-wrap {
      grid-column: 2/3;
      grid-row: 1/2;
    }
    .footer-item+.footer-item {
      margin-left: 0;
    }
  }
}

