        a {
          font-size: 20px;
          line-height: 24px;
          margin-bottom: 10px;
        }
      }
      &-nav {
        margin-bottom: 10px;
        ul {
          li + li {
            margin-top: 36px;
          }
          li {
            a {
              font-size: 16px;
              line-height: 20px;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
}

