.header {
  position: relative;
  z-index: 2;
  z-index: 999;
  &-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;
  }
  &__logo {
    width: 100%;
    max-width: 142px;
  }
  &-main {
    flex-grow: 1;
    display: flex;
    width: 100%;
  }
  &-back {
    &-menu {
      display: none;
      svg {
        fill: none;
        width: 40px;
        height: 26px;
      }
    }
  }
  &-nav {
    flex-grow: 1;
    display: flex;
    
    justify-content: flex-end;
    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      max-width: 828px;
      li {
        a {
          font-size: 18px;
          line-height: 22px;
          color: $secondary;
          text-decoration: none;
        }
      }
    }
  }
  &__gumb {
    position: relative;
    width: 44px;
    height: 22px;
    margin-left: 90px;
    span, &::before, &:after {
      position: absolute;
      content: '';
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 2px;
      border-radius: 2px;
      background-color: $secondary;
      background-color: transparent;
      transition: $cubic;
    }
    &::before, &:after {
      content: '';
      background-color: $secondary;
    }
    &::before {
      transform: translate(0px, -8px);
      // animation: gumbLineTop .5s ease-in-out forwards;
    }
    &::after {
      transform: translate(0px, 8px);
      // animation: gumbLineBottom .5s ease-in-out forwards;
    }
  }
  &-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(30, 37, 43, .8);
    opacity: 0;
    visibility: hidden;
    .container {
      height: 100%;
    }
    &-block {
      position: relative;
      bottom: 100%;
      background-color: $white;
      overflow: auto;
    }
    &-content {
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
      &-item.hide {
        display: none;
      }
      &-item {
        width: 100%;
        max-width: 960px;
        &-wrap {
          ul {
            column-count: 2;
            li + li {
              margin-top: 44px;
            }
            li {
              width: calc(100% - 20px);
              a {
                display: block;
                font-size: 22px;
                line-height: 38px;
                color: $secondary;
                text-decoration: none;
                width: fit-content;
              }
            }
          }
        }
      }
      &-item.contacts {
        max-width: 800px;
        .header-menu-content {
          &-item {
            &-wrap {
              height: 100%;
            }
          }
          &-contacts {
            display: flex;
            flex-direction: column;
            height: 100%;
            &-mail {
              margin-bottom: 80px;
              p {
                display: flex;
                align-items: center;
                span {
                  font-size: 18px;
                  line-height: 22px;
                  color: $secondary-light;
                  margin-right: 34px;
                }
                a {
                  text-decoration: none;
                  margin-right: 10px;
                  font-size: 22px;
                  line-height: 34px;
                  color: #09f;
                  // border-bottom: 1px solid #09f;
                }
              }
            }
            &-address {
              display: flex;
              justify-content: space-between;
              address {
                display: flex;
                flex-direction: column;
                font-style: normal;
                width: calc(50% - 28px);
                p {
                  font-size: 22px;
                  line-height: 28px;
                  color: $secondary;
                  margin-bottom: 28px;
                }
                span, a {
                  font-size: 18px;
                  line-height: 26px;
                  color: $secondary-light;
                  text-decoration: none;
                  margin-bottom: 16px;
                }
              }
            }
            &-social {
              display: flex;
              margin-top: auto;
              a {
                font-size: 18px;
                line-height: 34px;
                color: $primary;
                text-decoration: none;
                color: #09f;
                // border-bottom: 1px solid #09f;
              }
              a + a {
                margin-left: 10%;
              }
            }
          }
        }
      }
    }
    &-left {
      width: 100%;
      max-width: 455px;
      margin-right: 1%;
    }
    &-wrap {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      position: relative;
    }
    &-wrapper {
      display: flex;
      flex-grow: 1;
      padding-top: 148px;
      padding-bottom: 100px;
    }
    &-nav {
      li + li {
        margin-top: 64px;
      }
      li.item-active {
        & > span:first-child, a {
          &::after {
            width: 100%;
          }
        }
      }
      li {
        & > span:first-child {
          cursor: default;
        }
        span:first-child, a, a span:first-child {
          display: flex;
          align-items: center;
          font-size: 42px;
          line-height: 52px;
          color: $secondary;
          text-decoration: none;
        }
        & > span:first-child, a {
          position: relative;
          width: fit-content;
          &::after {
            position: absolute;
            content: '';
            top: calc(100% + 13px);
            left: 0;
            width: 100%;
            width: 0;
            height: 1px;
            background-color: $secondary;
            transition: $cubic;
          }
        }
        .btn-label {
          margin-left: 24px;
        }
      }
    }
    &-close {
      display: block;
      text-decoration: none;

      &-wrap {
        position: sticky;
        top: 44px;
      }
      svg {
        display: block;
        width: 32px;
        height: 32px;
      }
    }
  }
}
.header.sticky {
  height: 116px;
  .header-fixed {
    position: fixed;
    top: -84px;
    left: 0;
    width: 100%;
    background-color: #fff;
    transition: $cubic;
  }
  .header__logo {
    max-width: 110px;
  }
  .header-block {
    padding: 20px 0;
  }
}
.header.sticky.fixed {
  .header-fixed {
    top: 0;
    box-shadow: 0 0 15px rgba(30, 37, 43, .1);
  }
}