.btn {
  display: block;
  font-size: 18px;
  line-height: 20px;
  color: $secondary;
  text-decoration: none;
  text-align: center;
  outline: none;
  padding: 23px 34px;
  border-radius: 70px;
  border: 1px solid $secondary;
  transition: $cubic;
  background-color: transparent;
  width: fit-content;
  cursor: pointer;
}

.btn.disabled, .btn[disabled] {
  opacity: .4;
  pointer-events: none;
  cursor: default;
  background-color: $secondary;
  color: $white;
}

.btn-main-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  min-width: 68px;
  width: 68px;
  height: 68px;
  border-radius: 50%;
  border: 1px solid $secondary;
  background-color: transparent;
  transition: $cubic;
  svg {
    display: block;
    width: 30px;
    height: 19px;
    stroke: $secondary;
    fill: transparent;
    transition: $cubic;
  }
}

.btn-label {
  display: block;
  width: fit-content;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  padding: 9px 20px;
  border: 1px solid $secondary;
  color: $secondary;
  background-color: transparent; 
  border-radius: 90px;
  text-decoration: none;
  outline: none ;
  transition: $cubic;
}
.btn-label.article {
  color: $primary;
  border-color: $primary;
}
.btn-label.article.active {
  color: $white;
  background-color: $primary;
}
.btn-label.news {
  color: $primary-green;
  border-color: $primary-green;
}
.btn-label.popular {
  color: $primary-orange;
  border-color: $primary-orange;
}

.grid {
  display: grid;
}

.grid.type-1 {
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 64px;
  grid-row-gap: 44px;
}

.grid.type-2 {
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 75px;
  grid-row-gap: 65px;
}

// inputs
.input-wrap {
  position: relative;
  input {
    font-size: 18px;
    line-height: 20px;
    color: $secondary;
    padding-top: 24px;
    padding-bottom: 24px;
    border: none;
    outline: none;
    background-color: transparent;
    border-bottom: 1px solid $secondary;
    width: 100%;
    transition: $cubic;
  }
  input::placeholder {
    color: $secondary-light;
  }
  input:focus {
    border-bottom: 1px solid $primary;
  }
  span.error-message {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    left: 0;
    top: calc(100% + 10px);
    color: $danger;
  }
  .input-error {
    display: none;
    position: absolute;
    content: '';
    top: calc(100% + 10px);
    left: 0;
    color: $danger;
    font-size: 14px;
    line-height: 20px;
  }
}
.input-wrap.error {
  input {
    border-bottom: 1px solid $danger;
  }
  .input-error {
    display: block;
  }
}

.input-wrap + .input-wrap {
  margin-top: 39px;
}


// checkbox-form
.form-check {
  label {
    display: flex;
    align-items: center;
    cursor: pointer;
    width: fit-content;
    input {
      display: none;
    }
    input:checked + div {
      background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMjQiIGhlaWdodD0iMTYiIHZpZXdCb3g9IjAgMCAyNCAxNiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEgOC45MTMwNEw3LjY2NjY3IDE1TDIzIDEiIHN0cm9rZT0iIzAwOTlGRiIvPgo8L3N2Zz4K");
      background-position: center center;
      background-repeat: no-repeat;
      border: 1px solid $primary;
    }
    div {
      border: 1px solid $secondary-light;
      min-width: 38px;
      width: 38px;
      height: 38px;
      margin-right: 24px;
    }
    p {
      font-size: 18px;
      line-height: 20px;
      color: $secondary;
      user-select: none;
    }
  }
}
.progress-bar {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: 12px;
  width: 100%;
  progress {
    display: block;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    -webkit-appearance: none;
   appearance: none;
    &::-webkit-progress-bar {
      background-color: transparent;
    }
  }
}
.progress-bar.type-1 {
  background-color: $secondary;
  progress {
    &::-webkit-progress-value {
      background-color: $primary;
      border-radius: 0px;
    }
  }
}
.progress-bar.type-2 {
  background-color: $primary;
  progress {
    &::-webkit-progress-value {
      background-color: $secondary;
      border-radius: 0px;
    }
  }
}
.fancybox-container .fancybox-slide--html {
  padding: 0;
}
.popup-form {
  display: none;
  width: 100%;
  height: 100%;
  &__title {
    font-weight: 600;
    font-size: 44px;
    line-height: 64px;
    color: $secondary;
    margin-top: 10px;
  }
  &-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  &-block {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1424px;
    padding: 0 15px;
  }
  &-content {
    flex-grow: 1;
    width: 100%;
    max-width: 854px;
    padding-left: 30px;
  }
  .form-check {
    margin-top: 64px;
    margin-bottom: 64px;
  }
  .fancybox-button {
    width: 32px;
    height: 32px;
    background-image: url("data:image/svg+xml;base64,");
    outline: none;
    border: none;
    top: 44px;
    right: 5%;
    svg {
      display: none;
    }
  }
}


.link-line {
  position: relative;
  &::after {
    position: absolute;
    content: '';
    top: 100%;
    left: 0;
    height: 1px;
    z-index: 99;
    transition: $cubic;
  }
}
.link-line.type-1 {
  &::after {
    width: 0;
    background-color: $secondary;
  }
}
.link-line.type-2 {
  &::after {
    width: 100%;
    background-color: $primary;
  }
}

@media screen and (max-width: 1280px) {
  .popup-form {
    &__title {
      font-size: 36px;
    }
    &-wrap {
      display: block;
    }
    &-block {
      flex-direction: column;
      margin: 0 auto;
    }
    &-content {
      max-width: 100%;
      padding-left: 0;
      margin-top: 40px;
      padding-bottom: 50px;
    }
  }
}

@media (min-width: 1024px) {
  .btn:hover {
    background-color: $secondary;
    color: $white;
  }
  .wrap-icon-hover:hover .btn-main-icon {
    background-color: $secondary;
    svg {
      stroke: $white;
    }
  }
  a.btn-label.article:hover {
    color: $white;
    background-color: $primary;
  }
  a.btn-label.news:hover {
    color: $white;
    background-color: $primary-green;
  }
  a.btn-label.popular:hover {
    color: $white;
    background-color: $primary-orange;
  }
}

@media screen and (max-width: 768px) {
  .progress-bar {
    height: 8px; 
  }
}

@media screen and (max-width: 575px) {
  .popup-form {
    padding: 64px 16px;
    &__title {
      font-size: 32px;
      line-height: 42px;
      margin-top: 0;
    }
    &-block {
      padding: 0;
      max-width: 100%;
    }
    &-content {
      margin-top: 30px;
    }
    .form-check {
      margin-top: 30px;
      margin-bottom: 30px;
    }
    .fancybox-button {
      width: 20px;
      height: 20px;
      top: 16px;
      right: 16px;
      background-size: contain;
    }
  }
}