// animation blocks 
@keyframes gumbLineTop {
  50% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0px, 0px) rotate(45deg);
  }
}
@keyframes gumbLineBottom {
  50% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0px, 0px) rotate(-45deg);
  }
}
@keyframes teamShowCard {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes btnAction {
  0% {
    transform: translateX(0);
  }
  70% {
    transform: translateX(-80px);
  }
  100% {
    transform: translateX(0);
  }
}