body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  min-width: 375px;
}

body.hidden {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1740px;
  padding: 0 30px;
  margin: 0 auto;
}


h1 {
  font-weight: 600;
  font-size: 44px;
  line-height: 64px;
  color: $secondary;
}

h2 {
  font-size: 56px;
  line-height: 72px;
  font-weight: 600;
  color: $secondary;
}