.section-main {
  position: relative;
  background-image: url('../img/section-main-bg.jpg');
  background-repeat: no-repeat;
  height: 100vh;
  min-height: 900px;
  background-position: center center;
  background-size: cover;
  margin-top: -116px;
  padding-top: 116px;
  overflow: hidden;
  .container {
    height: 100%;
  }
  &-block {
    display: flex;
    height: 100%;
  }
  &-bottom {
    position: absolute;
    content: '';
    right: 0;
    bottom: 0;
    width: 40%;
    max-width: 765px;
    background-color: $white;
    padding-top: 64px;
    padding-left: 3.5%;
    padding-right: 6%;
    // transition: $cubic;
    &-block {
      padding-top: 54px;
      padding-bottom: 54px;
      border-top: 1px solid $secondary;
      border-bottom: 1px solid $secondary;
    }
    &-label {
      margin-bottom: 24px;
    }
    &-content {
      display: flex;
      justify-content: space-between;
      text-decoration: none;
      p {
        font-size: 28px;
        line-height: 38px;
        color: $secondary;
        width: 100%;
        max-width: 375px;
        margin-right: 10px;
      }
    }
  }
  &-middle {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
  }
  &-photo {
    display: flex;
    justify-content: center;
    width: calc(44.3% - 25px);
  }
  &-content {
    width: calc(55.7% - 25px);
    padding-top: 8%;
    height: 100%;
    h1 {
      font-weight: 600;
      font-size: 44px;
      line-height: 64px;
      color: $secondary;
      max-width: 807px;
      width: 100%;
      margin-bottom: 48px;
    }
  }
}
.services {
  padding-top: 160px;
  padding-bottom: 80px;
  &-grid.grid.type-1 {
    .services-grid-item {
      // min-height: 517px;
      border: 1px solid transparent;
      transition: $cubic;
      .grid-item-block {
        display: flex;
        flex-direction: column;
        height: 100%;
      }
      &-top {
        margin-bottom: 10px;
        &-icon {
          margin-bottom: 50px;
        }
        &-text {
          &__title {
            display: block;
            width: fit-content;
            font-size: 28px;
            line-height: 38px;
            color: $secondary;
            margin-bottom: 26px;
            text-decoration: none;
          }
        }
      }
      &-bottom {
        display: flex;
        justify-content: flex-end;
        margin-top: auto;
      }
    }
    .services-grid-item:nth-child(even) {
      background-color: #F7F9FB;
    }
    .services-grid-item:first-child {
      .grid-item-block {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 100%;
      }
    }
    .services-grid-item:not(:first-child) {
      padding: 12.4%;
    }
  }
}
.about-me {
  padding-top: 80px;
  padding-bottom: 80px;
  overflow-x: hidden;
  &-block {
    display: flex;
    justify-content: space-between;
    .about-me-left {
      width: 34%;
      padding-top: 15px;
      p {
        font-weight: 600;
        font-size: 36px;
        line-height: 44px;
        color: $primary;
      }
    }
    .about-me-right {
      width: 66%;
      p {
        font-weight: 600;
        font-size: 44px;
        line-height: 64px;
        color: $secondary;
        margin-bottom: 48px;
        span {
          color: $primary;
        }
      }
    }
  }
}
.evidence {
  padding-top: 160px;
  padding-bottom: 170px;
  background-color: #F7F9FB;
  h2 {
    margin-bottom: 84px;
  }
  &-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 60px;
    &-item {
      &__title {
        font-weight: 600;
        font-size: 44px;
        line-height: 72px;
        color: $primary;
        margin-bottom: 32px;
      }
      &__desc {
        font-size: 28px;
        line-height: 46px;
        color: $secondary;
      }
    }
  }
}
.team {
  padding-top: 80px;
  padding-bottom: 160px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid.grid.type-1 {
    .team-grid-item.wrap-icon-hover {
      position: relative;
      background-color: $secondary;
      border: 1px solid transparent;
      transition: $cubic;
      .team-grid-item-img {
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
      .team-grid-item-content {
        display: flex;
        flex-direction: column;
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: $default;
        padding: 12%;
        background-color: $white;
        &__name {
          font-size: 28px;
          line-height: 36px;
          color: $secondary;
          margin-bottom: 24px;
        }
        &__label {
          margin-bottom: 34px;
        }
        &__text {
          font-size: 18px;
          line-height: 34px;
          color: $secondary;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        &-top {
          margin-bottom: 20px;
        }
        &-links {
          margin-top: auto;
          display: flex;
          align-items: center;
          justify-content: space-between;
          & > a:first-child {
            text-decoration: none;
            margin-right: 10px;
            font-size: 22px;
            line-height: 34px;
            color: $primary;
            // border-bottom: 1px solid $primary;
          }
        }
      }
    }
    .team-grid-item:last-child {
      .grid-item-block {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        a {
          font-size: 36px;
          line-height: 52px;
          color: $secondary;
          text-decoration: none;
          width: 100%;
          max-width: 240px;
          span {
            color: $primary;
          }
        }
      }
    }
  }
}
.buy {
  background-image: url('../img/buy-bg.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  &-block {
    display: flex;
    justify-content: flex-end;
  }
  &-item {
    width: calc(50% - 30px);
    padding-top: 110px;
    padding-bottom: 105px;
    &__text {
      font-size: 44px;
      line-height: 71px;
      font-weight: 600;
      color: $secondary;
      margin-bottom: 35px;
      span {
        color: $primary;
      }
    }
  }
}
.cases {
  padding-top: 160px;
  padding-bottom: 80px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid {
    &-item {
      &-img {
        position: relative;
        overflow: hidden;
        &::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, .6);
          opacity: 0;
          transition: $default;
        }
        img {
          object-fit: cover;
          object-position: center center;
          width: 100%;
          height: 100%;
          transition: transform 1s ease,opacity 0.5s ease 0.25s;
          transform: scale(1);
        }
      }
      &-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 32px;
        text-decoration: none;
      }
    }
    &__title {
      font-size: 36px;
      line-height: 44px;
      font-weight: 600;
      color: $secondary;
      flex-grow: 1;
      margin-right: 15px;
    }
  }
}
.view-videos {
  padding-top: 80px;
  padding-bottom: 80px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid {
    &-item {
      &-img {
        position: relative;
        margin-bottom: 38px;
        &::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, .6);
          opacity: 0;
          transition: $default;
        }
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
    }
    &__subtitle {
      font-size: 28px;
      line-height: 38px;
      color: $secondary;
    }
  }
  .play-video {
    position: absolute;
    content: '';
    right: 12%;
    bottom: 19%;
    transition: $default;
    z-index: 4;
    svg {
      display: block;
      width: 68px;
      height: 68px;
      stroke: $white;
      fill: transparent;
      transition: $default;
    }
  }
}
.read-more {
  padding-top: 80px;
  padding-bottom: 160px;
  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 85px;
    h2 {
      margin-right: 15px;
      flex-grow: 1;
    }
  }
  &-list {
    border-top: 1px solid $secondary;
    // border-bottom: 1px solid $secondary;
    &-item + &-item {
      border-top: 1px solid $secondary;
    }
    &-item:last-child {
      border-bottom: 1px solid $secondary;
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 53px;
      padding-bottom: 55px;
      &-text {
        flex-grow: 1;
        margin-right: 15px;
      }
      &__title {
        display: block;
        width: fit-content;
        font-size: 36px;
        line-height: 44px;
        color: $secondary;
        font-weight: 600;
        margin-top: 24px;
        text-decoration: none;
      }
    }
  }
}
.subscription {
  position: relative;
  background-image: url('../img/subscription-bg.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
  &-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 124px;
    padding-bottom: 124px;
  }
  &-item {
    width: 50%;
    &__title {
      font-weight: 600;
      font-size: 44px;
      line-height: 71px;
      color: $secondary;
      span {
        color: $primary;
        white-space: nowrap;
      }
    }
  }
  &-item:last-child {
    display: flex;
    justify-content: center;
  }
  &-form {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    max-width: 520px;
    .input-wrap {
      flex-grow: 1;
    }
    .btn {
      margin-left: -30px;
    }
  }
}
.connection {
  padding-top: 147px;
  padding-bottom: 160px;
  overflow-x: hidden;
  h2 {
    width: 100%;
    max-width: 600px;
    margin-bottom: 84px;
  }
  &-block {
    display: flex;
    justify-content: space-between;
  }
  &-text {
    padding-top: 13px;
    width: calc(47% - 20px);
    &-link {
      display: flex;
      align-items: center;
    }
    p {
      font-size: 22px;
      line-height: 27px;
      color: $secondary-light;
      margin-right: 36px;
    }
    a {
      text-decoration: none;
      font-size: 28px;
      line-height: 34px;
      color: #09f;
      // border-bottom: 1px solid #09f;
    }
  }
  &-form {
    width: calc(53% - 20px);
    form {
      width: 100%;
      max-width: 725px;
    }
    &-check {
      margin-top: 63px;
    }
    button {
      margin-top: 63px;
    }
  }
}